.header {
  position: relative;

  &__top {
    position: relative;
    margin-top: 18px;
    margin-bottom: 49px;
    display: flex;
    align-items: center;

    @include media("max", "xs") {
      top: 17px;
      left: 5px;
      align-items: flex-start;
    }

   .header-top__burger {
    .burger__toggle {
      display: block;
      width: 34px;
      padding: 10px 0;
      position: absolute;
      z-index: 1;
      background: none;
      font-size: 0;
      color: transparent;
      cursor: pointer;

      @include media("max", "xs") {
        width: 27px;
      }

      .burger-toggle__item {
        display: block;
        width: 100%;
        height: 2px;
        background: #222831;
        position: absolute;
        top: -1px;
        right: 0;
        left: 0;
        bottom: 13;
        margin: auto;
  
        transition: background .2s linear;
      }

      .burger-toggle__item::before,
      .burger-toggle__item::after {
        content: '';
        width: 100%;
        height: 2px;
        position: absolute;
        background: #222831;
        left: 0;
        z-index: 1;
        transition: transform .2s linear;
      }

      .burger-toggle__item::before {
        top: -9px;
      }
    
      .burger-toggle__item::after {
        bottom: -9px;
      }
    }
   }

   .header-top__logo {
     margin-left: 109px;

     @include media("max", "xs") {
      margin-left: 40px;
    }
     .logo {
       display: flex;

       img {
         max-width: 42px;
         max-height: 65.89px;

         @include media("max", "xs") {
          width: 23px;
          height: 36px;
          margin-left: 10px;
          margin-top: -18px;
        }
       }

       .logo__block {
        margin-left: 20px;

        img {
          margin-bottom: 9px;
          margin-top: 12px;
          margin-left: -1px;
          max-width: 213px;
          max-height: 17px;

          @include media("max", "xs") {
            width: 154px;
            height: 12px;
            margin-top: -6px;
            margin-left: -11px;
          }
        }

        .logo-block__text {
          font-size: 13px;
          line-height: 16px;
          color: #555555;

          @include media("max", "xs") {
            display: none;
          }
        }
     }
    }
   }

   .header__form {

    @include media("max", "xs") {
      position: absolute;
      top: 36px;
      left: -104px;
    }
     .form {
      margin-left: 75px;
       .form__search {
        position: relative;
        background-color: #F6F6F6;

         .search__input {
          width: 849px;
          height: 54px;
          display: block;
          background-color: transparent;
          border: none;
          color: #73737B;
          font-size: 15px;
          line-height: 19px;
          padding-left: 65px;
          position: relative;
          z-index: 10;
          cursor: pointer;

          @include media("max", "xs") {
            width: 427px;
            padding-right: 10px;
          }
         }
       }
       .form__search::after {
          content: "";
          position: absolute;
          top: 50%;
          left: 30px;
          transform: translateY(-50%);
          width: 17.25px;
          height: 17.25px;
          background-repeat: no-repeat;
          background-position: center;
          background-size: contain;
          background-image: url(../img/favicon/search.svg);
       }
     }
   }

   .header__icons {
    margin-left: 67px;
    margin-top: 17px;

    @include media("max", "xs") {
      margin-left: 0;
      margin-right: 0;
      margin-top: 0;
      position: absolute;
      right: 2px;
    }

    .icons__btn {
      background: none;
      cursor: pointer;

      @include media("max", "xs") {
    
      }
    }

    .icons-btn__delivery {
      @include media("max", "xs") {
        display: none;
      }
    }

    .icons-btn__phone {
      display: none;
      @include media("max", "xs") {
        display: block;
        position: absolute;
        left: -114px;
        top: -11px;
      }
    }

    .icons-btn__user {
      margin-left: 27px;
      margin-bottom: 14px;

      @include media("max", "xs") {
        margin: 0;
        position: absolute;
        left: -75px;
        top: -13px;
      }
    }

    .icons-btn__bag {
      margin-left: 25px;
      margin-top: 2px;

      @include media("max", "xs") {
        margin: 0;
        position: absolute;
        left: -30px;
        top: -12px;
      }

      .btn-bag__show {
        position: relative;

        .show-num {
          position: absolute;
          padding-left: 5px;
          padding-right: 5px;
          padding-top: 1.8px;
          padding-bottom: 1.8px;
          right: -13px;
          top: -9px;
          background: #1DBCD2;
          font-weight: 700;
          font-size: 10px;
          line-height: 13px;
          color: #FFFFFF;
          border-radius: 50%;

          @include media("max", "xs") {
            display: none;
          }
        }
      }
    }
   }
  }

  &__bottom {

    @include media("max", "xs") {
      display: none;
    }

    .contacts__menu {
      display: flex;
      align-items: center;

      .contacts__menu-link {
        display: flex;  
      }

      .location {
        .menu-link__icon {
          margin-top: -1px;
        }
        .menu-link__city {
          margin-left: 15px;
          margin-top: 2px;
          cursor: pointer;
        }
      }

      .phone {
        margin-left: 167px;

        .menu-link__icon {
          margin-top: -3px;
        }

        .menu-link__tel {
          margin-left: 15px;
          line-height: 20px;
          color: #2B2E34;
        }
      }

      .email {
        margin-left: 168px;

        .menu-link__icon {
          margin-top: -1px;
        }

        .menu-link__email {
          margin-left: 15px;
          margin-top: -1px;
          line-height: 20px;
          color: #2B2E34;
        }
      }

      .request {
        margin-left: 169px;

        .menu-link__icon {
          margin-top: -2px;
        }

        .menu-link__request {
          margin-left: 16px;
          margin-bottom: 1px;
          cursor: pointer;
        }
      }

      .contacts {
        margin-left: 166px;

        .menu-link__icon {
          margin-top: -2px;
        }

        .menu-link__contacts {
          margin-left: 12px;
          margin-top: 2px;
          cursor: pointer;
        }
      }
    }
  }






.menu-mb {
  display: none;
  position: relative;
  z-index: 100;
  top: -85px;
  background: #E5E5E5;
  width: 100%;
  height: 100%;

  .mb {
    .logo {
      .logo__img {
        margin-top: 17px;
        margin-left: 3px;
        width: 23px;
        height: 36px;
      }

      .logo__text {
        margin-top: 29px;
        margin-left: 5px;
        width: 154px;
        height: 12px;
      }
    }

    .close {
      background: none;
      position: relative;
      left: 161px;
      top: 17px;
      cursor: pointer;
    }

    .mb-col1{
      margin-top: 63px;

      .filters-block__col1 {
        img {
          margin-left: 4px;
          margin-top: 1px;
        }
        .btn-open {
          margin-left: 14px;
          background: none;
          font-weight: 400;
          font-size: 16px;
          line-height: 20px;
          transition: 0.4s;
        }

        .btn-open:after {
          width: 13px;
          height: 8px;
          margin-left: auto;
          background-image: url("data:image/svg+xml,%3csvg width='13' height='8' viewBox='0 0 13 8' fill='none' xmlns='http://www.w3.org/2000/svg'%3e %3cpath d='M1 1L6.5 6.84173L12 1' stroke='%232B2E34' stroke-width='1.7' stroke-linecap='round' stroke-linejoin='round'/%3e %3c/svg%3e");
          background-repeat: no-repeat;
          font-weight: bold;
          float: right;
          margin-left: 23px;
          margin-top: 9px;
          content: '';
          position: relative;
          left: 68px;
        }
        .body-open {
          margin-left: 3px;
          overflow: hidden;
        }
        .body-open > p {
          margin-top: 30px;
          font-weight: 400;
          font-size: 14px;
          line-height: 18px;
        }

        .body-open  p:nth-child(2) {
          margin-top: 25px;
        }

        .body-open  p:nth-child(3) {
          margin-top: 25px;
        }

        .body-open  p:nth-child(4) {
          margin-top: 25px;
          padding-bottom: 42px;
          width: 99%;
          border-bottom: 1px solid rgba(0, 0, 0, 0.13);
        }
      }

      .block__col2, .block__col3, .block__col4 {
        margin-left: 4px;
        font-weight: 400;
        font-size: 16px;
        line-height: 20px;
      }

      .block__col2 {
        margin-top: 22px;
        border-bottom: 1px solid rgba(0, 0, 0, 0.13);;
        button {
          top: -22px;
          position: relative;
          left: -16px;
          background: none;
          margin-left: 15px;
          margin-top: 2px;
          width: 98%;
        }
      }

      .block__col3 {
        margin-top: 22px;
        width: 99%;
        height: 72px;
        width: 376px;
        border-bottom: 1px solid rgba(0, 0, 0, 0.13);
        img {
          margin-top: 14px;
        }
        button {
          background: none;
          margin-left: 14px;
          margin-top: 2px;
          position: relative;
          text-align: start;
          top: -35px;
          left: 27px;
          line-height: 24px;
        }
      }

      .block__col4 {
        margin-top: 22px;
        width: 99%;
        height: 44px;
        width: 376px;
        border-bottom: 1px solid rgba(0, 0, 0, 0.13);
        img {
          margin-top: 1px;
          margin-left: 1px
        }
        button {
          background: none;
          margin-left: 14px;
          margin-top: 2px;
          position: relative;
          text-align: start;
          top: -4px;
          left: 2px;
          line-height: 24px;
        }
      }
    }

    .nav-mb {
      margin-top: 42px;
      margin-left: 4px;
      .mb-item {
        margin-bottom: 30px;
        .item__link {
          font-weight: 400;
          font-size: 14px;
          line-height: 18px;
          cursor: pointer;
          color: #2B2E34;
        }
      }
    }

    .nav-mb li:nth-child(1) {
      margin-bottom: 28px;
    }
    .nav-mb li:nth-child(2) {
      margin-bottom: 29px;
    }

    .nav-mb li:nth-child(3) {
      margin-bottom: 28px;
    }
    .nav-mb li:nth-child(4) {
      margin-bottom: 28px;
    } 

    .nav-mb li:nth-child(5) {
      margin-bottom: 28px;
    } 

    .nav-mb li:nth-child(6) {
      margin-bottom: 0;
      padding-bottom: 75px;
    } 
  }
}

.open-burger-mb {
  display: block;
}

}