.footer {
  margin-top: 49px;
  .footer__container {
    display: flex; 
    padding-bottom: 52px;

    @include media("max", "xs") {
      display: block;
      padding-bottom: 48px;
    }
    
    .footer__contacts {
      .footer__logo {
        .logo {
          display: flex;

          @include media("max", "xs") {
            margin-top: 1px;
            margin-left: 5px;
          }

          .logo__block {
            img {
              margin-top: 15px;
              margin-left: 20px;
            }
            .logo-block__text {
              margin-top: 9px;
              margin-left: 21px;
              font-weight: 400;
              font-size: 13px;
              line-height: 16px;
              color: #555555;
            }

            
          @include media("max", "xs") {
            margin-left: -3px;
          }
          }
        }
      }

      .footer__phone {
        display: flex;
        margin-top: 57px;

        @include media("max", "xs") {
          margin-top: 39px;
          margin-left: 5px;
        }

        .menu-link__tel {
          display: block;
          margin-top: 2px;
          margin-left: 12px;
          font-weight: 400;
          font-size: 18px;
          line-height: 20px;
          text-align: right;
          color: #2B2E34;

          @include media("max", "xs") {
            margin-left: 13px;
          }
        }
      }

      .footer__time {
        display: flex;
        margin-top: 18px;
        font-weight: 400;
        font-size: 16px;
        line-height: 28px;
        color: #7E838C;

        @include media("max", "xs") {
          margin-left: 6px;
        }

        .time-time {
          margin-left: 6px;
        }
      }

      .footer__email {
        margin-top: 55px;
        display: flex;

        
        @include media("max", "xs") {
          margin-top: 45px;
          margin-left: 6px;
        }

        .menu-link__email {
          margin-left: 14px;
          display: block;
          font-weight: 400;
          font-size: 18px;
          line-height: 20px;
          color: #2B2E34;
        }

        img {
          margin-left: -2px;
        }
        
      }

      .footer__social {
        margin-top: 59px;
        font-weight: 500;
        font-size: 18px;
        line-height: 20px;
        color: #2B2E34;
        margin-left: -1px;

        @include media("max", "xs") {
          margin-top: 41px;
          margin-left: 3px;
        }

        .social__block {
          margin-top: 26px;
          display: flex;

          .social__vk, .social__instagram {
            font-weight: 400;
            font-size: 16px;
            line-height: 25px;
            color: #7E838C;

            span {
              position: relative;
              bottom: 6px;
              left: 7px;
            }
          }

          .social__instagram {
            margin-left: 30px;

            span {
              margin-left: -2px;
            }
          }
        }
      }
    }

    .footer-nav {

      @include media("max", "xs") {
        margin-left: 0;
      }
      .list-item-title {
        margin-bottom: 26px;
        font-weight: 500;
        font-size: 18px;
        line-height: 23px;
        color: #2B2E34;

        @include media("max", "xs") {
          margin-bottom: 15px;
        }
      }

      .list-item {
        margin-bottom: 0px;

        .item-link {
            font-weight: 400;
          font-size: 16px;
          line-height: 33px;
          color: #7E838C;
        }
      }
    }

    .footer-nav__col-1 {
      margin-left: 203px;
      margin-top: 9px;
      width: 276px;

      @include media("max", "xs") {
        margin-left: 4px;
        margin-top: 54px;
      }
    }

    .footer-nav__col-2 {
      margin-left: 167px;
      margin-top: 8px;
      width: 249px;

      @include media("max", "xs") {
        margin-left: 4px;
        margin-top: 37px;
      }

      .list-item-title {
        margin-left: 2px;
        margin-top: 1px;
      }

      .list-item {
        margin-left: 5px;
      }
    }

    .footer-nav__col-3 {
      margin-left: 202px;
      margin-top: 9px;
      width: 149px;

      @include media("max", "xs") {
        margin-left: 4px;
        margin-top: 37px;
      }
    }
  }

  .footer-hr {
    width: 1520px;
    margin-bottom: 51px;
    border-bottom: 1px solid rgba(217, 218, 225, 0.8);

    @include media("max", "xs") {
      width: 374px;
    }
  }

  .footer__copyright {
    margin-top: -2px;
    display: flex;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    color: #7E838C;

    @include media("max", "xs") {
      display: block;
    }

    .copyright-text {
      @include media("max", "xs") {
        position: relative;
        top: 10px;
        left: 5px;
      }
    }

    .copyright-politics {
      margin-left: 166px;

      @include media("max", "xs") {
        margin-left: 0;
        position: relative;
        top: 41px;
        left: 5px;
      }
    }

    .copyright-treatment {      
      margin-left: 167px;

      @include media("max", "xs") {
        margin-left: 0;
        position: relative;
        top: 71px;
        left: 5px;
      }
    }

    div {
      position: relative;
      display: flex;
      margin-left: 166px;
      margin-top: -1px;

      @include media("max", "xs") {
        width: 190px;
        margin: 0;
        top: 136px;
        left: 4px;
      }

      img {
        margin-left: 19px;
        margin-top: -5px;
      }

      .request {
        position: absolute;
        right: -128px;
        top: -2px;
        margin-left: 6px;
        font-weight: 700;
        font-size: 18px;
        line-height: 27px;
        font-family: 'Circe';
        color: #2B2E34;

        span {
          position: relative;
          font-weight: 400;
          left: -2px;
        }

        @include media("max", "xs") {
          right: -91px;
          top: -2px;
        }
      }
    }

  }
}