.main {
  flex: 1 1 auto;
  margin-top: 30px;
  background: #fbfbfb;
  
  .promo {
    position: relative;
    margin-bottom: 46px;
    background: linear-gradient(91.79deg, #1DBCD1 16.94%, #3DDCF1 83.14%);

    @include media("max", "xs") {
      margin-top: 10px;
    }

    &__container {
      display: flex;
      max-height: 294px;
      overflow: hidden;

      @include media("max", "xs") {
        max-height: 505px;
      }

      .promo__text {
        min-width: 566px;
        @include media("max","xs"){
          text-align: center;
          min-width: 385px;
        }
        &-title {
          padding-top: 82px;
          font-weight: 600;
          font-size: 42px;
          line-height: 53px;
          color: #FFFFFF;

          @include media("max", "xs") {
            padding-top: 41px;
            font-weight: 600;
            font-size: 28px;
            line-height: 36px;
          }
        }
        &-subtitle {
          padding-top: 23px;
          padding-bottom: 81px;
          padding-left: 1px;
          line-height: 27px;
          color: #FFFFFF;

          @include media("max", "xs") {
            font-size: 15px;
            line-height: 22px;
            padding-top: 11px;
            padding-left: 1px;
            width: 330px;
            margin: auto;
          }
        }

        
        &-subtitle  br {
          @include media("max", "xs") {
            display: none;
          }
        }
      }
  
      .promo__img {
        position: relative;
        left: -41px;
        bottom: 53px;

        @include media("max", "xs") {
          left: -438px;
          bottom: -256px;
        }
        .promo-img__fridge {
          @include media("max", "xs") {
            width: 478px;
          }
        }
        
      }

      .promo__botton {
        margin-left: -9px;

        @include media("max", "xs") {
          position: absolute;
          margin: 0;
          top: 97px;
          right: 90px;
        }

        .promo-button__more{
          margin-top: 110px;
          margin-right: 2px;
          padding: 27.5px 50.11px 27.5px 46px;
          border-radius: 2px;
          display: flex;
          align-items: center;
          font-weight: 500;
          font-size: 18px;
          line-height: 23px;
          letter-spacing: 0.02em;
          background: #FFFFFF;
          color: #2B2E34;
          cursor: pointer;

          .arrow-block {
            display: inline-block;
            position: relative;
            margin-left: 2px;
            .arrow-left {
              position: relative;
              background: #2B2E34;
              display: block;
              left: 0;
              top: -1.5px;
              width: 10px;
              height: 2.6px;
              display: block;
              transform: rotate(45deg);
              border-radius: 2px;
            }
            .arrow-right {
              position: relative;
              display: block;
              background: #2B2E34;
              left: 0;
              top: 1.5px;
              width: 10px;
              height: 2.6px;
              transform: rotate(-45deg);
              border-radius: 2px;
            }
            
          }

          .arrow__block-one {
            opacity: 0.2;

            @include media("max", "xs") {
              .arrow-left {
                left: 5px;
              }
              .arrow-right {
                left: 5px;
              }
            }
          }

          .arrow__block-two {
            opacity: 0.5;

            @include media("max", "xs") {
              .arrow-left {
                left: 3px;
              }
              .arrow-right {
                left: 3px;
              }
            }
          }

          .arrow__block-three {
            opacity: 0.8;

            @include media("max", "xs") {
              .arrow-left {
                left: 0;
              }
              .arrow-right {
                left: 0;
              }
            }
          }

          .arrow__block-four {
            margin-right: 34px;

            @include media("max", "xs") {
              margin-right: 29px;

              .arrow-left {
                left: -3px;
              }
              .arrow-right {
                left: -3px;
              }
            }
          }

          @include media("max", "xs") {
            padding: 17px 37px 17px 27px;
            font-weight: 400;
            font-size: 15px;
            line-height: 19px;
            letter-spacing: 0.02em;
          }
        }

        .promo-button__more p {
          @include media("max", "xs") {
            margin-right: 5px;
          }
        }
      }
    }
  }

