            }
          }

          [slider] {
            width: 300px;
            position: relative;
            height: 5px;
            margin: 41px 0 10px 0;
          }
          
          [slider] > div {
            position: absolute;
            left: 0px;
            right: 0px;
            height: 5px;
          }
          [slider] > div > [inverse-left] {
            position: absolute;
            left: 0;
            height: 3px;
            border-radius: 3px;
            background-color: #DEE1E8;
            
          }
          
          [slider] > div > [inverse-right] {
            position: absolute;
            right: 0;
            height: 3px;
            border-radius: 3px;
            background-color: #DEE1E8;
          
          }
          
          
          [slider] > div > [range] {
            position: absolute;
            left: 0;
            height: 3px;
            border-radius: 3px;
            background-color: #38D3E8;
          }
          
          [slider] > div > [thumb] {
            position: absolute;
            top: -9px;
            z-index: 2;
            height: 18px;
            width: 3px;
            text-align: left;
            margin-left: -1.5px;
            cursor: pointer;
            background-color: #38D3E8;
            border-radius: 3px;
            outline: none;
          }
          
          [slider] > input[type=range] {
            position: absolute;
            //pointer-events: none;
            -webkit-appearance: none;
            z-index: 3;
            height: 14px;
            top: -2px;
            width: 100%;
            opacity: 0;
          }
          
          div[slider] > input[type=range]:focus::-webkit-slider-runnable-track {
            background: transparent;
            border: transparent;
          }
          
          div[slider] > input[type=range]:focus {
            outline: none;
          }
          
          div[slider] > input[type=range]::-webkit-slider-thumb {
            pointer-events: all;
            width: 3px;
            height: 18px;
            border-radius: 0px;
            border: 0 none;
            background: red;
            -webkit-appearance: none;
          }
          
          div[slider] > input[type=range]::-ms-fill-lower {
            background: transparent;
            border: 0 none;
          }
          
          div[slider] > input[type=range]::-ms-fill-upper {
            background: transparent;
            border: 0 none;
          }
          
          div[slider] > input[type=range]::-ms-tooltip {
            display: none;
          }
          
          [slider] > div > [sign] {
            opacity: 1;
            position: absolute;
            margin-left: -11px;
            top: 15px;
            z-index:3;
            color: #2B2E34;
            width: 37px;
            height: 28px;
            align-items: center;
            -webkit-justify-content: center;
            justify-content: center;
            text-align: center;
          }
          /*
          [slider] > div > [sign]:after {
            position: absolute;
            content: '';
            left: 0;
            border-radius: 16px;
            top: 19px;
            border-left: 14px solid transparent;
            border-right: 14px solid transparent;
            border-top-width: 16px;
            border-top-style: solid;
            border-top-color: #d02128;
          }*/
          
          [slider] > div > [sign] > span {
            font-size: 18px;
            font-weight: 400;
            line-height: 18px;
          }
          
        }
